export default {
	name: 'ControlBar',
	computed:{		

		btnType() {
			let	_this = this
			if(this.$store.getters.isLock){ //如果lock为true，返回变化之前的值
			console.log("hold住了")
				return _this.hold
			}else{
				//当lock为false，保存变化之前的值
				_this.hold = this.$store.getters.btnType
				console.log("没hold住")
				return this.$store.getters.btnType
			}
			
		}
	},
	data() {
		return {
			onTouch: -1,
			hold: null
		}
	},
	methods: {
		tapYes() {
			this.$emit('tapYes')
		},
		tapNo() {
			this.$emit('tapNo')
		},
		tapNext(){
			this.$emit('tapNext')
		},
		tapNoClear(){
			this.$emit('tapNoClear')
		},
		touchStar(e) {
			this.onTouch = e.currentTarget.dataset.id
		},
		touchEnd() {
			this.onTouch = -1
		}
	}

}
